.mobilemenu-sty-cover {
  color: white;
}

.main-nav {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  text-align: center;
  background: #171221;
  height: 100vh;
  opacity: 0;
  z-index: 99 !important;
  visibility: hidden;
  transition: all .375s;
}

.main-nav.is-open {
  opacity: 1;
  z-index: 100;
  visibility: visible;
}

/* Yellow band effect */
.main-nav::before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: -15px;
  border-right: 1px solid #e83e8c;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
  -webkit-transform: skew(-14deg) translateX(-120%);
          transform: skew(-14deg) translateX(-120%);
  transition: all .275s .1s;
}

.main-nav.is-open::before {
  -webkit-transform: skew(-14deg) translateX(0);
          transform: skew(-14deg) translateX(0);
}

/* Skewing effect on menu links */
.main-nav ul {
  display: inline-flex;
  flex-direction: column;
  height: 100%;
  /* Should be 100%, but we have a notice message :D */
  align-items: flex-end;
  justify-content: center;
  -webkit-transform: translateX(-18%) skew(-16deg);
          transform: translateX(-18%) skew(-16deg);
}

.main-nav li {
  display: block;
  margin: .5rem 0;
  text-align: right;
  -webkit-transform: skew(16deg);
          transform: skew(16deg);
}

/* Apparition effect on links */
.main-nav span {
  opacity: 0;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.main-nav.is-open span {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.main-nav li:nth-child(1) span {
  transition: all 275ms 175ms;
}

.main-nav li:nth-child(2) span {
  transition: all 275ms 225ms;
}

.main-nav li:nth-child(3) span {
  transition: all 275ms 275ms;
}

.main-nav li:nth-child(4) span {
  transition: all 275ms 325ms;
}

.main-nav li:nth-child(5) span {
  transition: all 275ms 375ms;
}

/* Decoration */
.main-nav ul,
.main-nav li {
  list-style: none;
  padding: 0;
}

.main-nav span {
  display: block;
  padding: 12px 0;
  color: white;
  font-size: 1.4em;
  text-decoration: none;
  font-weight: bold;
  transition: 1s all ease;
}

.main-nav .mobile-home {
  color: #e83e8c;
}

.main-nav span:hover {
  color: #e83e8c;
  -webkit-transform: scale(1.001);
          transform: scale(1.001);
}

.main-nav span:focus {
  color: #e83e8c;
  -webkit-transform: scale(1.001);
          transform: scale(1.001);
}

.main-nav span:active {
  color: #e83e8c;
  -webkit-transform: scale(1.001);
          transform: scale(1.001);
}

.mobile-home {
  color: #e83e8c;
}

/* Burger Style: @see: https://codepen.io/CreativeJuiz/full/oMZNXy */
.open-main-nav {
  position: absolute;
  top: 15px;
  padding-top: 20px;
  right: 15px;
  z-index: 1000;
  background: none;
  border: 0;
  cursor: pointer;
}

.open-main-nav:focus {
  outline: none;
}

.burger {
  position: relative;
  display: block;
  width: 28px;
  height: 4px;
  margin: 0 auto;
  background: #e83e8c;
  -webkit-transform: skew(5deg);
          transform: skew(5deg);
  transition: all .275s;
}

.burger:after,
.burger:before {
  content: '';
  display: block;
  height: 100%;
  background: #e83e8c;
  transition: all .275s;
}

.burger:after {
  -webkit-transform: translateY(-12px) translateX(-2px) skew(-20deg);
          transform: translateY(-12px) translateX(-2px) skew(-20deg);
}

.burger:before {
  -webkit-transform: translateY(-16px) skew(-10deg);
          transform: translateY(-16px) skew(-10deg);
}

/* Toggle State part */
.is-open .burger {
  -webkit-transform: skew(5deg) translateY(-8px) rotate(-45deg);
          transform: skew(5deg) translateY(-8px) rotate(-45deg);
}

.is-open .burger:before {
  -webkit-transform: translateY(0px) skew(-10deg) rotate(75deg);
          transform: translateY(0px) skew(-10deg) rotate(75deg);
}

.is-open .burger:after {
  -webkit-transform: translateY(-12px) translateX(10px) skew(-20deg);
          transform: translateY(-12px) translateX(10px) skew(-20deg);
  opacity: 0;
}

/* MENU Text part */
.burger-text {
  display: block;
  font-size: .675rem;
  letter-spacing: .05em;
  margin-top: .5em;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
  color: #e83e8c;
}

.header-socialmedia-nav-mobile {
  position: absolute;
  height: 40px;
  bottom: 50px !important;
  width: 100%;
}

@media (max-height: 550px) {
  .main-nav {
    top: 25px;
  }
  .main-nav li {
    display: block;
    margin: .1rem 0;
    text-align: right;
    -webkit-transform: skew(16deg);
            transform: skew(16deg);
  }
  .main-nav span {
    display: block;
    padding: 7px 0;
    color: white;
    font-size: 1.4em;
    text-decoration: none;
    font-weight: bold;
  }
}
